"use client";
import { useActionState } from "react";
import { signupAction } from "@/lib/actions/SignupAction";
import { useRouter, useSearchParams } from "next/navigation";
import { useEffect } from "react";

const AuthPage = () => {
  const router = useRouter();
  const searchParams = useSearchParams();
  const mode = searchParams.get("mode");

  const [formState, formAction, isLoading] = useActionState(signupAction, {
    message: [],
    user: {
      name: "",
      email: "",
      password: "",
    },
    success: false,
  });

  useEffect(() => {
    if (formState.success) {
      router.push("/");
    }
  }, [formState, router]);

  function toggleMode() {
    if (mode === "login") {
      router.push("/auth?mode=signup");
    } else {
      router.push("/auth?mode=login");
    }
  }

  return (
    <div className="relative flex h-screen flex-col bg-black md:mt-[120px] md:items-center md:bg-transparent">
      <form
        action={formAction}
        className="relative mt-24 space-y-6 rounded bg-black/75 py-8 px-6 md:mt-0 md:max-w-md md:px-14"
      >
        <h1 className="text-3xl font-semibold text-white text-center">
          {mode === "login" ? "Sign In" : "Create Account"}
        </h1>

        {formState.message && formState.message.length > 0 && (
          <div className="p-3 bg-red-600 text-white rounded text-sm">
            <ul>
              {formState.message.map((ms, index) => (
                <li key={index}>{ms}</li>
              ))}
            </ul>
          </div>
        )}

        <div className="space-y-4">
          {mode === "signup" && (
            <label className="inline-block w-full">
              <input
                name="name"
                type="text"
                placeholder="Name"
                defaultValue={formState.user?.name}
                className="input"
                disabled={isLoading}
              />
            </label>
          )}

          <label className="inline-block w-full">
            <input
              name="email"
              type="email"
              placeholder="Email"
              defaultValue={formState.user?.email}
              className="input"
              required
              disabled={isLoading}
            />
          </label>

          <label className="inline-block w-full">
            <input
              name="password"
              type="password"
              placeholder="Password"
              defaultValue={formState.user?.password}
              className="input"
              required
              disabled={isLoading}
            />
          </label>
        </div>

        <button
          type="submit"
          disabled={isLoading}
          className={`w-full rounded bg-[#e50914] py-3 font-semibold ${
            isLoading ? "opacity-60 cursor-not-allowed" : ""
          }`}
        >
          {isLoading
            ? mode === "login"
              ? "Signing In..."
              : "Signing Up..."
            : mode === "login"
            ? "Sign In"
            : "Sign Up"}
        </button>

        <div className="flex justify-between items-center text-[#737373] text-sm">
          <div className="flex items-center">
            <input
              type="checkbox"
              id="remember"
              className="mr-1"
              disabled={isLoading}
            />
            <label htmlFor="remember">Remember me</label>
          </div>
          <a href="#" className="hover:underline">
            Need help?
          </a>
        </div>

        <div className="text-[#737373]">
          {mode === "login" ? (
            <>
              New to Netflix?{" "}
              <button
                type="button"
                onClick={toggleMode}
                className="text-white hover:underline"
                disabled={isLoading}
              >
                Sign up now
              </button>
            </>
          ) : (
            <>
              Already have an account?{" "}
              <button
                type="button"
                onClick={toggleMode}
                className="text-white hover:underline"
                disabled={isLoading}
              >
                Sign in now
              </button>
            </>
          )}
        </div>

        <div className="text-[#737373] text-xs">
          <p>
            This page is protected by Google reCAPTCHA to ensure you&#39;re not
            a bot.{" "}
            <a href="#" className="text-blue-500 hover:underline">
              Learn more.
            </a>
          </p>
        </div>
      </form>
    </div>
  );
};

export default AuthPage;
